import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
export default {
	data(){
		return{
			logined:false,
			//消息数字角标
			show_badge:true,
			badgeText:" "
		}
	},
	
	onShow() {
		var me = this;
		var userinfo = me.getGlobalUser("userInfos");
		if(userinfo != null){
			me.userinfo = userinfo;
			me.logined = userinfo.logined;
		}else{
			me.logined = false;
			me.userinfo = {};
		}
	},
	
	methods:{
		clickItem:function(){
			var me = this;
			me.show_badge = false;
		}
	},
	components: {
		uniList,
		uniListItem
	}
}
